@mixin r($width) {
  @media only screen and (max-width: $width+ "px") {
    @content; } }

@mixin coverdiv() {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%; }

@mixin cover() {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%; }

// vertical-align at center
@mixin vcenter() {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%); }

//simple gradient
@mixin g($color1, $color2) {
  background: linear-gradient($color1, $color2); }

// TRANSITION
@mixin tr($what) {
  transition: $what; }

@mixin justify() {
  text-align: justify;
  line-height: 0;
  font-size: 0;
  text-align-last: justify;
  &:after {
    content: "";
    display: inline-block;
    width: 100%;
    height: 0px;
    visibility: hidden;
    overflow: hidden; } }

@mixin vertical() {
  text-align: center;
  font-size: 0;
  &:before {
    content: "";
    display: inline-block;
    vertical-align: middle;
    width: 0;
    height: 100%; } }

@mixin clr() {
  &:after {
    content: " ";
    display: table;
    clear: both; } }

@mixin rotate($var) {
  transform: rotate($var+ "deg"); }

@mixin blur($var) {
  filter: blur($var); }

@mixin scale($var) {
  transform: scale($var); }
